import { intercept } from 'mobx';
import { toUpper } from 'lodash';
import {
    BaseModel, identifiedBy, identifier, belongsTo, field,
} from './base';

function upperCaseOnChange(change) {
    change.newValue = toUpper(change.newValue);  // eslint-disable-line no-param-reassign
    return change;
}

@identifiedBy('sh/venue')
export default class Venue extends BaseModel {

    @identifier id;

    @field code = '';

    @field name = '';
    @field address = '';
    @field phone_number;

    @belongsTo({ model: 'lanes/asset', inverseOf: 'owner' }) logo;


    constructor(attrs) {
        super(attrs);
        intercept(this, 'code', upperCaseOnChange);
    }


    static get sharedCollection() {
        return this.$cachedCollection ||
            (this.$cachedCollection = this.Collection.create([], { fetch: true }));
    }
}



