import {
    BaseModel, identifiedBy, identifier, field, belongsTo,
} from './base';

@identifiedBy('sh/event')
export default class Event extends BaseModel {

    @identifier id;

    @field slug = '';

    @field title = '';
    @field sub_title = '';
    @field description = '';

    @field venue_id;

    @field({ type: 'date' }) visible_after;
    @field({ type: 'date' }) visible_until;

    @field({ type: 'date' }) onsale_after;
    @field({ type: 'date' }) onsale_until;

    @belongsTo({ model: 'sh/venue' }) venue;
    @belongsTo({ model: 'lanes/asset', inverseOf: 'owner' }) image;

}



