import { action, observable, computed, autorun } from 'mobx';
import { addFormFieldValidations, nonBlank, stringValue, setFieldValues } from 'lanes/lib/forms';

import React from 'react';
import { Row } from 'react-flexbox-grid';
import { forIn } from 'lodash';
import { observer }   from 'mobx-react';
import Box from 'grommet/components/Box';
import Button from 'grommet/components/Button';
import Warning from 'lanes/components/warning-notification';
import Field from 'lanes/components/form-field';
import Asset from 'lanes/components/asset';



import Query from 'lanes/models/query';

import Venue from '../../models/venue';
import Event from '../../models/event';

@observer
class EditForm extends React.PureComponent {

    static propTypes = {
        query:      React.PropTypes.instanceOf(Query).isRequired,
        event:      React.PropTypes.instanceOf(Event).isRequired,

        index:      React.PropTypes.number.isRequired,
        onComplete: React.PropTypes.func.isRequired,
        formState: React.PropTypes.shape({
            touchd: React.PropTypes.bool,
            valid:  React.PropTypes.bool,
        }).isRequired,

        fields: React.PropTypes.object.isRequired,
        setDefaultValues: React.PropTypes.func.isRequired,
    }

    static desiredHeight = 300

    static formFields = {
        title: nonBlank,
        sub_title: stringValue,
        description:  stringValue,
        venue_id:     nonBlank,
        onsale_until: stringValue,
        onsale_after: stringValue,

        visible_until: stringValue,
        visible_after: stringValue,
    }

    @computed get venues() {
        return Venue.sharedCollection.map(opt => ({ id: opt.id, label: opt.name }));
    }

    @computed get event() {
        return this.props.event;
    }

    componentWillMount() {
        this.props.setDefaultValues(this.event.serialize());
    }

    @action.bound
    onSave() {
        forIn(this.props.fields, (field, name) => (this.event[name] = field.value));
        this.event.save().then(this.onSaved);
    }

    @action.bound
    onSaved() {
        if (!this.event.errors) {
            this.onCancel();
        }
    }

    @action.bound
    onCancel() {
        this.props.onComplete();
    }

    @computed get isSavable() {
        return this.props.formState.valid && !this.event.syncInProgress;
    }

    render() {
        const { fields, style } = this.props;

        return (
            <div className="event-edit-form" style={{ ...style, height: 'auto' }}>
                <Warning message={this.event.errorMessage} />
                <Row middle='sm'>
                    <Field fields={fields} name="title" xs={6} lg={3} />
                    <Field fields={fields} name="sub_title" xs={6} lg={3} />

                    <Field fields={fields} name="description" xs={12} lg={6} />

                    <Field fields={fields}
                           name="venue_id" xs={12} lg={6}
                           type="select"
                           collection={this.venues}
                    />

                    <Field type="date" fields={fields} name="visible_after" lg={3} xs={6} />
                    <Field type="date" fields={fields} name="visible_until" lg={3} xs={6} />

                    <Field type="date" fields={fields} name="onsale_after" lg={3} xs={6} />
                    <Field type="date" fields={fields} name="onsale_until" lg={3} xs={6} />

                    <Asset model={this.event} name="image" lg={3} xs={6} />

                </Row>

                <Box
                    flex
                    margin="small"
                    direction="row"
                    justify="end"
                    pad={{ between: 'small' }}
                >
                    <Button label="Cancel" onClick={this.onCancel} accent />
                    <Button
                        label="Save"
                        onClick={this.isSavable ? this.onSave : null}
                        primary
                    />

                </Box>

            </div>
        );
    }
}

export default addFormFieldValidations(EditForm, 'desiredHeight');



